<template>
  <div class="upBag">
    <div class="backgroundBlockUp">
      <div class="block2" style="filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));">
        <img v-for="(imagesUP, idx) in imagesUP"
             v-bind:src="imagesUP.url"
             v-on:click="emitImg(imagesUP.url)"
             v-bind:key="idx"
             class="backgroundBlockImages"
        />
      </div>
    </div>
    <label class="upText upB" >
      <div class="upText">Завантажити власний фон
        <img class="upImg pos" src="@/assets/img/upB.svg" alt="downloads">
        <img class="upImg dis" src="@/assets/img/upBwhite.svg" alt="downloads">
      </div>
      <input multiple type="file"
             accept=".jpg, .jpeg, .png"
             @change="onFileChange"
             style="display:none">
    </label>
  </div>
</template>

<script>
import {mapActions} from 'vuex';

export default {
  methods: {
    ...mapActions(['EMIT_upBag']),
    onFileChange(e) {
      const file = e.target.files[0];
      this.imagesUP[0].url = URL.createObjectURL(file);
      const file1 = e.target.files[1];
      this.imagesUP[1].url = URL.createObjectURL(file1);
      const file2 = e.target.files[2];
      this.imagesUP[2].url = URL.createObjectURL(file2);
      const file3 = e.target.files[3];
      this.imagesUP[3].url = URL.createObjectURL(file3);
    },
    emitImg: function emitImg(data) {
      console.log(data)
      this.EMIT_upBag(data)
    },
  },
  data() {
    return {
      imagesUP: [
        { url: null },
        { url: null },
        { url: null },
        { url: null },
      ],
    };
  },
};
</script>

