<script>
import {mapActions, mapGetters} from 'vuex';

export default {
  data(){
    return{
      urlImgBag: 'https://online-fotoshop.ru/wp-content/uploads/bfi_thumb/dummy-transparent-o62bcwfxu7zofs36kb0sbh4wom52bbxxszhrx8zw4y.png',
      active1: false,
      active2: false,
      active3: false,
      active4: false,
      active5: false
    }
  },
  created() {
    this.SET_FRAMES();
  },
  computed: {
    ...mapGetters(['frame', 'frameOrientation', 'material', 'closedImg', 'colorsFrame', 'numberPostsFrame', 'mechanismBlockOne', 'mechanismBlockTwo', 'mechanismBlockThree', 'mechanismBlockFour', 'mechanismBlockFive', 'selectedMechanismBlock', 'horizontal']),
    ...mapGetters(['background']),
  },
  methods: {
    ...mapActions(['SET_FRAMES', 'EMIT_selectedMechanismBlock', 'EMIT_closedMechanismBlock']),
    mechanismBlock: function mechanismBlock(data){
      if(data === 'mechanismBlockOne'){
        this.active1 = true;
        this.active2 = false;
        this.active3 = false;
        this.active4 = false;
        this.active5 = false;

      }
      if(data === 'mechanismBlockTwo'){
        this.active1 = false;
        this.active2 = true;
        this.active3 = false;
        this.active4 = false;
        this.active5 = false;
      }
      if(data === 'mechanismBlockThree'){
        this.active1 = false;
        this.active2 = false;
        this.active3 = true;
        this.active4 = false;
        this.active5 = false;
      }
      if(data === 'mechanismBlockFour'){
        this.active1 = false;
        this.active2 = false;
        this.active3 = false;
        this.active4 = true;
        this.active5 = false;
      }
      if(data === 'mechanismBlockFive'){
        this.active1 = false;
        this.active2 = false;
        this.active3 = false;
        this.active4 = false;
        this.active5 = true;
      }
      if(data === 'mechanismBlockNone'){
        this.active1 = false;
        this.active2 = false;
        this.active3 = false;
        this.active4 = false;
        this.active5 = false;
      }
      this.EMIT_selectedMechanismBlock(data);
    },
    mechanismBlockClosed: function mechanismBlockClosed(data){
      this.EMIT_closedMechanismBlock(data);
    }
  },
};
</script>

